.container {
  width: 100%;
  height: 257px;
  display: flex;
  flex-direction: column;
  background: #fff;
  box-shadow: 0 -1px 0 #e2e6e9;
}

@media (min-width: 640px) {
  .container {
    height: 96px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 1200px) {
  .container {
    height: 96px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navigation {
  display: flex;
  flex-direction: column;
  gap: 16px;

  &__link {
    text-decoration: none;
    font-family: "Mont", sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 12px;
    line-height: 11px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #89939a;
  }
}

@media (min-width: 640px) {
  .navigation {
    display: flex;
    flex-direction: row;
    gap: 13px;
  }
}

@media (min-width: 1200px) {
  .navigation {
    display: flex;
    flex-direction: row;
    gap: 107px;
  }
}

.button-back {
  margin-top: 40px;

  &__anchor {
    display: flex;
    justify-content: center;
    text-decoration: none;
    color: #89939a;
    font-family: "Mont", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 15px;
  }
}

@media (min-width: 640px) {

  .button-back {
    margin-right: 80px;
    margin-top: 0;
  }
}

@media (min-width: 1200px) and (max-width: 1200px) {

  .button-back {
    margin-right: 80px;
    margin-top: 0;
  }

}

@media (min-width: 1201px) {

  .button-back {
    margin-right: 200px;
    margin-top: 0;
  }

}

.logo {
  margin-top: 34px;
  margin-bottom: 32px;
}

@media (min-width: 640px) {
  .logo {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 32px;
  }
}

@media (min-width: 1200px) and (max-width: 1201px) {
  .logo {
    margin-bottom: 0;
    margin-left: 32px;
  }
}

@media (min-width: 1201px) {
  .logo {
    margin-bottom: 0;
    margin-left: 152px;
  }
}
